.mainBody {
  /* background-image: url(../images/background_black.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center; */
  background-color: black;
  font-family: 'Arimo', sans-serif;
  color: rgb(223, 223, 223);
}

.app {
  width: 80%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-rows: 1fr 7fr;
  overflow-y: hidden;
  height: 100vh;
  background-color: #383838;
  border-left: 1px solid gray;
  border-right: 1px solid gray;
  overflow: hidden;
}

/* css for component Header */
.app-header {
  grid-column: 1/4;
  background-color: rgb(32, 32, 32);
  line-height: 2.3rem;
  border-bottom: 1px solid black;
  box-shadow: 0 3px 3px -3px rgb(54, 53, 53);
}
.app-header h1{
  text-align: center;
  margin-top: 0.24em;
  font-size: 2.2em;
  letter-spacing: 0.03em;
}
.app-header-top {
  display: -webkit-flex;
  display: flex;
}

/* css for component Levels */
.app-levels {
  border-right: 1px solid gray;
  padding: 0.3em 1em;
}
.app-levels-selectLevels {
  font-size: 1.5rem;
  margin-top: 1.2rem;
  margin-bottom: 1.2rem;
  text-align: center;
}

.app-levels-allLevels {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-row-gap: 1em;
  margin-bottom: 1rem;
}
.app-levels-stars {
  position: absolute;
  top: 82%;
  left: 51%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: -webkit-flex;
  display: flex;
}
.app-levels-level {
  width: 3rem;
  height: 3rem;
  margin: 0 auto;
  border-radius: 6px;
  background-color: rgb(20, 89, 146);
  cursor: pointer;
  position: relative;
  border: 1px solid rgb(153, 147, 147);
}
.app-levels-descriptions {
  padding-bottom: 30px;
}
.app-levels-descriptions img {
  width: 1.6rem;
}
.app-levels-descriptions span{
  font-size: 1.1rem;
  font-family: 'Arimo', sans-serif;
  letter-spacing: 0.03rem;
}
/* levelName on all levles */
.app-levels-level p{
  color: rgb(224, 203, 14);
  font-size: 1.4rem;
  position: absolute;
  top: -5%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.app-levels-unlockButton {
  width: 100%;
}
.app-levels-content-img {
  position: absolute;
  top: 17%;
  left: 16%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.app-levels-level img{
  width: 0.7rem;
  padding: 0 0.08rem;
}


/* css for component Game*/
.app-game {
  padding: 2.8rem 0 0 1rem;
  /* padding: 3rem 0 1rem 1.5rem; */
  display: -webkit-flex;
  display: flex;
}
/* css for component Board*/
.app-board {
  width: 70%;
}
.gameBoard {
  margin: 0 auto;
  display: grid;
}
.app-board-copyright {
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 1px;
  text-align: center;
  margin-top: 2rem;
  font-size: 1rem;
  font-weight: 500;
  color: rgb(180, 180, 24);
}

.app-board-copyright img {
  width: 13px;
  padding-right: 5px;
}
/* css for component GameInfo*/
.app-game-gameInfo {
  padding: 0 2rem;
}
.app-game-gameInfo-info {
  padding-left: 14%;
  font-size: 1.3rem;
  letter-spacing: 1px;
}
.gameInfo-buttons-group1 {
  margin-bottom: 2.2rem;
  margin-top: 2.2rem;
}
.red-span {
  color: rgb(238, 217, 25);
  padding-right: 2px;
}
input[type=button]{
  width: 100%;
  height: 2.4rem;
  margin-bottom: 0.7rem;
  font-size: 1.05rem;
  letter-spacing: 0.03em;
  border: 1px solid gray;
  border-radius: 4px;
  background-color: rgb(20, 89, 146, 0.9);
  color: rgb(243, 237, 237);
  cursor: pointer;
}
input[type=button]:hover {
  box-shadow: -2px -2px rgb(85, 82, 82);
}

.gameInfo-buttons-group2 {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 0.3rem;
}
.gameInfo-buttons-group2 img {
  width: 3rem;
}
.gameInfo-buttons-group2 img:hover {
  cursor: pointer;
  box-shadow: 0 4px 2px -3px rgb(209, 209, 11);
}
.all-arrows-up {
  grid-column: 2/3;
}
.all-arrows-left {
  grid-column: 1/2;
}
/* css for component Footer */
.app-footer {
  grid-column: 1/4;
}
/* css for component Dialog */
.app-dialog {
  background-color: rgb(221, 214, 214);
}
.app-dialog p{
 text-align: center;
 font-family: 'Arimo', sans-serif;
 font-size: 1.1rem;
 letter-spacing: 0.017rem;
}
.app-dialog-stars {
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
}
.app-dialog-stars img{
  margin-right: 7px;
  width: 35px;
}
